'use strict'
import 'swiper/css/swiper.min.css'
import 'luminous-lightbox/dist/luminous-basic.min.css'
import Swiper from 'swiper'
import { Luminous } from 'luminous-lightbox';
import { throttle, debounce } from 'throttle-debounce'

/**
 * 商品詳細ページの処理を提供します
 */

export default () => {
  const zoomList = document.querySelectorAll('.js-zoom');
  zoomList.forEach(el => {
    new Luminous(el);
  })
  const thumbList = document.querySelector('.product-detail__base--thumb')
  const mainSwiper = new Swiper('.product-detail__base--mainimg', {
    slidesPerView: 1,
    effect: 'slide',
    loop: true,
    speed: 500,
    navigation: {
      nextEl: '.product-detail__base--mainimg .swiper-button-next',
      prevEl: '.product-detail__base--mainimg .swiper-button-prev'
    }
  })
  mainSwiper.on('slideChange', function () {
    thumbList.querySelector('div.is-current').classList.remove('is-current')
    thumbList.querySelectorAll('div')[mainSwiper.realIndex].classList.add('is-current')
  })
  thumbList.querySelectorAll('div')[mainSwiper.realIndex].classList.add('is-current')
  thumbList.querySelectorAll('div').forEach(THUMB => {
    THUMB.addEventListener('click', () => {
      const selectIndex = [].slice.call(thumbList.querySelectorAll('div')).indexOf(THUMB)
      mainSwiper.slideToLoop(selectIndex)
    })
  })

  const rankItems = document.querySelectorAll('.c-product-list--ranking')
  const rankSwiper = Array(document.querySelectorAll('.c-product-list--ranking').length)
  rankItems.forEach(function (elem, index) {
    if (window.innerWidth <= 768) {
      rankSwiper[index] = new Swiper(elem, {
        slidesPerView: 'auto',
        effect: 'slide',
        speed: 500,
        pagination: {
          el: elem.querySelector('.swiper-pagination'),
          clickable: true
        }
      })
    }
  })
  window.addEventListener('resize',
    debounce(150, () => {
      rankItems.forEach(function (elem, index) {
        if (window.innerWidth <= 768) {
          if (rankSwiper[index]) {

          } else {
            rankSwiper[index] = new Swiper(elem, {
              slidesPerView: 'auto',
              effect: 'slide',
              speed: 500,
              pagination: {
                el: elem.querySelector('.swiper-pagination'),
                clickable: true
              }
            })
          }
        } else {
          if (rankSwiper[index]) {
            rankSwiper[index].destroy()
            rankSwiper[index] = undefined
          }
        }
      })
    }),
    false
  )
}
